    "Cet outil aide les organisations et les mouvements de la société civile à devenir plus inclusifs, plus diversifiés et plus sensibles aux besoins, aux identités et aux origines de leur population. Nous croyons aux meilleures intentions des peuples pour réfléchir sur eux-mêmes, leur culture et leurs pratiques, en engageant des dialogues difficiles lorsque cela est nécessaire. Interagir avec cet outil n'est pas une panacée pour résoudre la discrimination, le racisme ou le harcèlement sur le lieu de travail. Il fournit plutôt un aperçu de la position de votre organisation sur le spectre du modèle de maturité, avec des suggestions sur la manière de se développer dans les domaines qui ont besoin d'une attention particulière. Nous espérons que cet outil aidera les organisations de la société civile à devenir des modèles inspirants et des champions de la diversité et de l'inclusion dans leurs communautés, contribuant ainsi à notre mission commune de construire un monde meilleur, plus juste, plus équitable et plus humain.",
