    "Il existe très peu de littérature et d'études de cas sur la mise en place unique d'organisations de la société civile à utiliser comme exemples pratiques pour le secteur. Au cours de l'année dernière, CIVICUS a testé la mise en œuvre de pratiques diverses et inclusives, et son impact sur la santé organisationnelle globale, par le biais d'un programme pilote d'un an avec différents acteurs de la société civile. 8 membres de CIVICUS ont été sélectionnés parmi 400 candidatures présentant une diversité régionale et contextuelle. Une fois les participants au programme pilote sélectionnés, CIVICUS a jumelé chaque organisation avec un consultant externe pour qu'elle subisse un audit sur la diversité et l'inclusion. L'audit a été conçu comme un exercice de bilan : il s'agissait d'examiner ce dont l'organisation disposait déjà en termes de politiques et de pratiques d'inclusion et de déterminer les lacunes flagrantes à combler. Chaque organisation a reçu un rapport complet identifiant ses performances et des recommandations sur ce qu'elle peut améliorer, ainsi que des suggestions sur la manière d'aborder les lacunes.",
