  aboutParagraphOne:
    'Ha habido muy poca literatura y estudios de casos sobre el establecimiento único de organizaciones de la sociedad civil para utilizar como ejemplos prácticos para el sector. Durante el año pasado, CIVICUS probó la aplicación de prácticas diversas e inclusivas, y su impacto en la salud organizacional, en general, a través de un programa piloto con diferentes actores de la sociedad civil. Se seleccionaron 8 miembros de CIVICUS de entre 400 solicitudes con diversidad regional y contextual. Una vez seleccionados los participantes en el programa piloto, CIVICUS emparejó a cada organización con un consultor externo para someterse a una auditoría de diversidad e inclusión. La auditoría se diseñó para ser un ejercicio de inventario: examinar lo que la organización ya tiene en términos de políticas y prácticas de inclusión y qué lagunas evidentes deben abordarse. Cada organización recibió un informe completo en el que se identificaba que ya estaban haciendo bien las cosas y se formulaban recomendaciones sobre lo que podían mejorar, con sugerencias sobre cómo podían abordar las lagunas.',
